html,
body {
  height: 100%;
}

body {
  font-size: rem(16px);
  line-height: 1.42;
  position: relative;
  color: black;
}

.container {
  margin: 0 auto;
  position: relative;
  max-width: rem(1100px);
  width: 95%;
  height: 100%;
  max-width: 68.75rem;
  display: flex;
  flex-direction: column;
}

.wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.maincontent {
  height: 100%;
  transition: transform 1s;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  font-weight: normal;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

ul {
  padding: 0;
  margin: 0;
}

ul li {
  list-style: none;
}

p {
  padding: 0;
  margin: 0;
}

footer {
  position: relative;
  width: 100%;
}

* {
  box-sizing: border-box;
}

.section {
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.section__title {
  color: #fff;
  font-size: 3.75rem;
  font-family: "Alegreya Sans";
  font-weight: 700;
  line-height: 60px;
  text-transform: uppercase;
}

.pic {
  object-fit: cover;
  max-width: 100%;
  max-height: 100%;
}

.welcome {
  background: url("../img/chocco/fon.png") center center no-repeat;
  background-size: cover;
  position: relative;
}

.welcome__cont {
  position: relative;
}

.welcome__cont:after {
  content: '';
  background-image: url("../img/chocco/blackberry.png");
  background-size: cover;
  width: 23rem;
  height: 8.1rem;
  position: absolute;
  top: 89.5%;
  right: 6%;
  position: absolute;
}

.welcome__cont:before {
  content: url("../img/chocco/list.png");
  top: 92%;
  left: 3%;
  position: absolute;
}

.welcome__content {
  display: flex;
  transform: scaleX(-1);
}

.welcome__container {
  display: flex;
  flex: 1;
  justify-content: center;
  align-items: center;
}

.welcome__left,
.welcome__right {
  float: left;
  width: 50%;
  transform: scaleX(-1);
}

.welcome__left {
  padding-top: 6.125rem;
  padding-left: 5rem;
  padding-right: 1.4375rem;
}

.welcom-describe {
  width: 25.25rem;
}

.section__title {
  margin-bottom: 1.375rem;
}

.welcome__desc {
  color: #fff;
  font-family: "Alegreya Sans";
  font-size: 19px;
  font-weight: 400;
  margin-bottom: 3.375rem;
}

.welcome__links {
  margin-bottom: 5.875rem;
}

.welcome__img {
  display: inline-block;
  position: relative;
  left: 50%;
  transform: translateX(-50%);
  max-width: 100%;
  max-height: 100%;
}

.header {
  padding-top: 1.5rem;
  padding-left: 4.9375rem;
  padding-right: 4.9375rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  color: #fff;
  font-family: "Museo Sans Cyrl";
  font-size: 24px;
  font-weight: 500;
  line-height: 21px;
  text-transform: uppercase;
}

.nav__list {
  width: 100%;
  display: flex;
}

.nav__item {
  white-space: nowrap;
  display: inline-block;
  vertical-align: middle;
  margin-right: 1.875rem;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  font-size: 18px;
  font-family: "Alegreya Sans";
  font-weight: 400;
  line-height: 30px;
  text-decoration: none;
  color: #fff;
}

.nav__link:hover {
  text-decoration: underline;
}

.address,
.contacts {
  font-style: normal;
  position: relative;
  white-space: nowrap;
}

.contacts__phone {
  text-decoration: none;
  color: #fff;
  font-family: Alegreya Sans;
  font-size: 24px;
  font-weight: 400;
  line-height: 30px;
  text-transform: uppercase;
}

.contacts__phone--footer {
  color: #0f5a47;
  margin-left: 5%;
  text-align: left;
}

.contacts__phone {
  position: relative;
}

.contacts__phone:before {
  content: url("../img/phone/phone_white.png");
  left: -11%;
  top: -19%;
  position: absolute;
}

.contacts__phone--footer:before {
  content: url("../img/phone/phone_green.png");
  left: -5%;
  top: -5%;
  position: absolute;
}

.welcome__link {
  padding: 1.5rem 2.125rem;
  border-radius: 35px;
  border: 1px solid #fff;
  background-color: #fff;
  color: #0f5a47;
  font-family: "Alegreya Sans";
  font-size: 19px;
  font-weight: 700;
  text-transform: uppercase;
  text-decoration: none;
  box-shadow: 2px 10px 20px rgba(0, 0, 0, 0.2);
}

.information__list {
  display: flex;
  padding-bottom: 2.9375rem;
}

.information__item {
  margin-right: 9%;
  display: flex;
}

.information__item:last-child {
  margin-right: 0;
}

.information__text-paragraph {
  color: #fff;
  font-family: "Alegreya Sans";
  font-size: 12px;
  font-weight: 400;
}

.information__icon {
  margin-right: 0.5625rem;
  overflow: hidden;
  flex: 0 0 auto;
}

.icon-information {
  width: 3.125rem;
  height: 2.625rem;
  fill: #b0f7ff;
}

.pic {
  display: block;
  object-fit: cover;
  max-width: 100%;
  max-height: 100%;
}

.why-chocco {
  position: relative;
  background-image: url(../img/why/list.png);
  background-repeat: no-repeat;
  background-position: 0px;
  text-align: center;
  display: flex;
  align-items: center;
}

.why-chocco:after {
  content: '';
  background-image: url("../../img/reviews/chocco.png");
  background-size: cover;
  width: 10rem;
  height: 19.7rem;
  top: 73%;
  left: 0;
  position: absolute;
}

.why-chocco__container {
  display: flex;
  align-items: center;
  justify-content: center;
}

.why-chocco__tit {
  margin-bottom: 1.4375rem;
}

.why-chocco__desc {
  color: #666666;
  font-family: "Alegreya Sans";
  font-size: 18px;
  font-weight: 400;
  margin-bottom: 3.5625rem;
}

.why-chocco__list {
  display: flex;
}

.why-chocco__item {
  padding: 0 3.3rem;
  display: flex;
  flex-direction: column;
  position: relative;
}

.why-chocco__item:after {
  content: url("../img/why/dods.png");
  left: 91.5%;
  top: 19%;
  position: absolute;
}

.why-chocco__item:last-child:after {
  content: "";
}

.why-chocco__icon {
  width: 7.8125rem;
  height: 7.8125rem;
  border-radius: 50%;
  background-color: #f2f5f4;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  margin-bottom: 2.1875rem;
}

.why-chocco-title {
  color: #000000;
  font-family: "Alegreya Sans";
  font-size: 27px;
  font-weight: 700;
  margin-bottom: 1.5625rem;
}

.section__title--black {
  color: #000000;
  font-size: 3rem;
}

.why-chocco__text-paragraph {
  color: #666666;
  font-family: "Alegreya Sans";
  font-size: 16px;
  font-weight: 300;
}

.icon-why,
.why-chocco__icon--pic {
  max-width: 100%;
  max-height: 100%;
  width: 4.375rem;
  height: 4.375rem;
}

.bar-chocco {
  background-color: #f8faf9;
}

.bar-chocco__container {
  display: flex;
  flex-direction: row;
  position: relative;
  align-items: center;
  justify-content: center;
}

.bar-chocco__container:before {
  content: '';
  background-image: url("../../img/bar-chocco/list2.png");
  background-size: cover;
  width: 5rem;
  height: 6.1rem;
  top: 29%;
  right: 94%;
  position: absolute;
}

.bar-chocco__container:after {
  content: '';
  background-image: url("../../img/bar-chocco/list.png");
  background-size: cover;
  width: 8rem;
  height: 5.7rem;
  top: 67%;
  right: 50%;
  position: absolute;
}

.btn__icon {
  width: 100%;
  height: 100%;
}

.slider-arrow {
  position: absolute;
  top: 50%;
  width: 1.8125rem;
  height: 2.75rem;
  z-index: 10;
}

.slider-arrow__prev {
  transition: 0.6s ease;
  left: -1.25rem;
}

.slider-arrow__next {
  transition: 0.6s ease;
  right: -1.25rem;
}

.slider {
  width: 100%;
  position: relative;
}

.slider__content {
  position: relative;
  height: 35rem;
  overflow: hidden;
}

.slider__list {
  white-space: nowrap;
  font-size: 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}

.slider__item {
  display: inline-block;
  height: 100%;
  white-space: normal;
  width: 100%;
}

.slider__item--content {
  display: flex;
  position: relative;
}

.slider__col {
  flex: 1;
  display: flex;
  flex-direction: column;
  position: relative;
}

.slider__img {
  flex: 1;
  width: 100%;
  align-items: center;
  display: flex;
  justify-content: center;
}

.slider__col {
  width: 50%;
  position: relative;
}

.slider__col--img {
  margin-right: 5rem;
}

.structure__icon {
  object-fit: cover;
  max-width: 100%;
  max-height: 100%;
}

.structure {
  width: 5.625rem;
  height: 5.625rem;
  background-color: #61c4d9;
  border-radius: 0.3rem;
  align-items: center;
  display: flex;
  flex-direction: column;
  margin-bottom: 2.3125rem;
  position: relative;
  text-decoration: none;
  outline: none;
}

.structure:hover {
  background-color: #00accf;
}

.structure:hover .structure__drop {
  opacity: 1;
  left: 5.625rem;
}

.structure__img {
  width: 3.1875rem;
  height: 3.1875rem;
  fill: #fff;
  margin-top: 0.625rem;
  margin-bottom: 0.2rem;
}

.structure__title {
  font-family: "Alegreya Sans";
  font-size: 14px;
  font-weight: 600;
  text-transform: uppercase;
  color: #fff;
}

.slider__title {
  font-family: "Alegreya Sans";
  font-size: 36px;
  font-weight: 700;
  text-transform: uppercase;
  line-height: 2.5rem;
  margin-bottom: 1.375rem;
  max-width: 23rem;
}

.slider__description {
  max-width: 23rem;
}

.slider__paragraph {
  font-family: "Alegreya Sans";
  font-size: 18px;
  font-weight: 400;
  color: #666666;
  margin-bottom: 2.9375rem;
  text-align: left;
}

.slider__calories-weight {
  display: flex;
  font-family: "Alegreya Sans";
  font-size: 18px;
  font-weight: 400;
  margin-bottom: 2.1875rem;
  text-align: left;
}

.slider__calories {
  margin-right: 3.4375rem;
}

.calories__number,
.weight__number {
  font-weight: 700;
}

.slider__order {
  display: flex;
  align-items: center;
}

.slider__price {
  margin-right: 2rem;
  font-family: "Alegreya Sans";
  font-size: 60px;
  font-weight: 300;
  line-height: 60px;
  text-transform: uppercase;
}

.structure__drop {
  position: absolute;
  top: 0;
  left: -625rem;
  background-color: rgba(255, 255, 255, 0.9);
  border-radius: 5px;
  opacity: 0;
  transition: opacity .3s;
  padding: 0.625rem;
}

.structure__list {
  display: table;
  width: 16.875rem;
}

.structure__item {
  display: table-row;
}

.structure__badge,
.structure__text,
.weight {
  display: table-cell;
  vertical-align: middle;
  height: 2.1875rem;
  padding: 0.625rem;
}

.struc__icon {
  width: 2.1875rem;
  height: 2.1875rem;
}

.structure__text {
  font-family: "Alegreya Sans";
  font-size: 16px;
  font-weight: 500;
  line-height: 24px;
  color: #000000;
}

.weight {
  font-family: "Alegreya Sans";
  font-size: 16px;
  font-weight: 400;
  line-height: 24px;
  color: #000000;
}

.slider__item {
  -webkit-animation-name: fade;
  -webkit-animation-duration: 1.5s;
  animation-name: fade;
  animation-duration: 1.5s;
}

@-webkit-keyframes fade {
  from {
    opacity: .4;
  }

  to {
    opacity: 1;
  }
}

@keyframes fade {
  from {
    opacity: .4;
  }

  to {
    opacity: 1;
  }
}

.team {
  position: relative;
  text-align: center;
  display: flex;
  align-items: center;
}

.team:before {
  content: url("../img/team/blueberry.png");
  bottom: 50%;
  right: 0;
  position: absolute;
}

.team:after {
  content: url("../img/team/raspberry.png");
  top: 90%;
  left: 8%;
  position: absolute;
  z-index: 40;
}

.team__container {
  padding-top: 4.5625rem;
  display: flex;
  align-items: center;
}

.accordeon-team__list {
  display: flex;
}

.accordeon-team__item {
  padding: 0 0.25rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  position: relative;
  /*
    &--active,
    &:hover {
        .accordeon-team__content {
            max-height: 5.75rem;
            @include tablets {
                max-height: 7.75rem;
            }
        } 
        .accordeon-team__btn--name {
            &:after {
                content: url('../img/team/up.png');
                margin-left: 2%;
                position: absolute;
            }
        }
    }*/
}

.accordeon-team__item.open .teamcontent {
  max-height: 5.75rem;
}

.accordeon-team__item.open .btn__name:after {
  content: url("../img/team/down.png");
  margin-left: 2%;
  top: 14.9rem;
  position: absolute;
  display: inline-block;
  transform: rotate(180deg);
}

.accordeon-team__img--pic {
  object-fit: cover;
  max-width: 100%;
  max-height: 100%;
}

.team__tit {
  margin-bottom: 3.625rem;
}

.teamcontent {
  overflow: hidden;
  max-height: 0;
  width: 17.1875rem;
  transition: max-height .3s;
}

.accordeon-team__img {
  width: 10.75rem;
  height: 13rem;
  margin-bottom: 1.625rem;
}

.accordeon-team__btn {
  text-decoration: none;
}

.btn__name {
  font-family: "Alegreya Sans";
  font-size: 21px;
  font-weight: 500;
  color: #323232;
  margin-bottom: 0.8125rem;
  /*&:after {
        content: url('../img/team/down.png');
        margin-left: 2%;
        position: absolute;
    }*/
}

.btn__name:after {
  content: url("../img/team/down.png");
  margin-left: 2%;
  position: absolute;
}

.profession {
  font-family: "Alegreya Sans";
  font-size: 18px;
  font-weight: 500;
  color: #281e1e;
  margin-bottom: 0.75rem;
}

.accordeon-team__text {
  font-family: "Alegreya Sans";
  font-size: 18px;
  font-weight: 300;
  color: #666666;
}

.accordeon-team__people {
  display: flex;
